.loop {
  --time: 8s;
  place-content: center;
  place-items: center;
  margin: 0 auto;
  display: grid;
}

.loop__image {
  border: hidden;
  border-radius: 8px;
  margin: -36px auto 0;
  padding-left: 20px;
  padding-right: 20px;
  position: absolute;
}

@media screen and (min-width: 768px) {
  .loop__image {
    min-width: 768px;
    max-width: 1199px;
    margin-top: -49px;
    position: absolute;
  }
}

@media screen and (min-width: 1200px) {
  .loop__image {
    min-width: 1200px;
    max-width: 1600px;
    margin-top: -98px;
    padding-bottom: 200px;
    position: absolute;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .loop img {
    animation: rotate calc(var(--time) * 3) linear 0s infinite;
    grid-area: 1 / 1;
  }

  .loop img:nth-child(2) {
    animation-delay: calc(var(--time) * -2);
  }

  .loop img:nth-child(3) {
    animation-delay: calc(var(--time) * -1);
  }
}

@keyframes rotate {
  0%, 33.33% {
    opacity: 1;
  }

  33.34%, 100% {
    opacity: 0;
  }
}

.visually-hidden:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

