@import '../mixins';
@import './vars';
ul {
  list-style: none;
}
.container {
  max-width: 380px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
  @include tablet {
    max-width: 768px;
  }
  @include desktop {
    max-width: 1200px;
  }
}
.products {
  background-image: url(../images/Backgroud_mobile@1x.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  @media (min-device-pixel-ratio: 2),
    (-webkit-min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/Backgroud_mobile@2x.jpg);
  }
  @include tablet {
    background-image: url(../images/Background_tablet@1x.jpg);
    @media (min-device-pixel-ratio: 2),
      (-webkit-min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/Background_tablet@2x.jpg);
    }
    @include desktop {
      background-image: url(../images/Backgroud_desktop@1x.jpg);
      @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/Backgroud_desktop@2x.jpg);
      }
    }
    &__label {
      color: #131313;
      font-family: 'Montserrat', sans-serif;
      font-weight: 800;
      font-size: 18px;
      line-height: 1.2;
      text-transform: uppercase;
      text-align: center;
      padding-top: 60px;
      @include tablet {
        font-size: 22px;
      }
      @include desktop {
        font-size: 30px;
      }
    }
    &__title {
      color: $textBlack;
      font-family: 'Seymour One', sans-serif;
      font-weight: 400;
      font-size: 30px;
      line-height: 1.26;
      text-transform: uppercase;
      text-align: center;
      margin-top: 12px;
      @include tablet {
        font-size: 34px;
      }
      @include desktop {
        font-size: 36px;
      }
    }
    &__list {
      @include tablet {
        display: flex;
        gap: 16px;
      }
      @include desktop {
        display: flex;
        gap: 29px;
      }
    }
    &__name {
      color: $textBlack;
      font-family: 'Montserrat', sans-serif;
      font-weight: 700;
      font-size: 20px;
      line-height: 1.22;
      text-transform: uppercase;
      text-align: center;
      @include tablet {
        font-size: 22px;
      }
      @include desktop {
        font-size: 30px;
        margin-top: 165px;
      }
    }
    &__text {
      font-family: 'Montserrat', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 1.77;
      letter-spacing: 0.04em;
      text-align: center;
      margin-top: 40px;
      @include desktop {
        font-size: 16px;
        margin-top: 66px;
        width: 242px;
        margin: 0 auto;
      }
      @include tablet {
        width: 172px;
        margin: 0 auto;
      }
    }

    &__button {
      background: none;
      border: none;
      margin: 0 auto;
      display: flex;
      justify-content: flex-end;
      cursor: pointer;
    }
  }
}
.products__item {
  width: 280px;
  height: 420px;
  border-radius: 24px;
  margin: 0 auto;
  margin-top: 250px;
  margin-bottom: 60px;
  @include tablet {
    width: 224px;
    height: 548px;
  }
  @include desktop {
    width: 370px;
    height: 536px;
  }
  &--ice-cream {
    background-color: #aec6a5;
  }
  &--ice-coffee {
    background-color: #efd478;
  }
  &--milkshakes {
    background-color: #d6936d;
  }
}
.ice-cream {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  position: absolute;
  translate: 40% -50%;
}
.ice-cream::before {
  box-shadow: 0 8px 30px rgba(10, 136, 23, 0.25);
  background-image: linear-gradient(rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7));
}
.ice-coffee {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  position: absolute;
  translate: 40% -50%;
}
.ice-coffee::before {
  box-shadow: 0 8px 30px rgba(153, 114, 23, 0.25);
  background-image: linear-gradient(rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7));
}
.milkshakes {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  position: absolute;
  translate: 40% -50%;
}
.milkshakes::before {
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  background-image: linear-gradient(rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7));
}
.backdrop {
  position: fixed;
  inset: 0;
  z-index: 9999;
  cursor: not-allowed;
  transform: scale(1);
  background-color: #00000033;
}
.products-modal {
  position: fixed;
  inset: 0;
  margin: auto;
  background-color: $textWhite;
  border: 2px solid $secondaryBrandColor;
  border-radius: 10px;
  width: 280px;
  height: 968px;
  @include desktop {
    width: 568px;
    height: 996px;
  }
  &__close {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20px;
    width: 20px;
    top: 12px;
    right: 12px;
    background-color: $textWhite;
    border: none;
    position: absolute;
    @include desktop {
      width: 24px;
      height: 24px;
    }
  }
  &__title {
    font-family: 'Seymour One', sans-serif;
    font-weight: 400px;
    font-size: 20px;
    line-height: 1.4;
    color: $textBlack;
    display: block;
    margin: 0 auto;
    margin-top: 28px;
    width: 142px;
    height: 56px;
    @include desktop {
      margin-top: 32px;
    }
  }
  &__img {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    @include desktop {
      margin-top: 24px;
    }
  }
  &__button {
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    line-height: 1.22;
    width: 132px;
    height: 42px;
    font-size: 14px;
    background-color: $secondaryBrandColor;
    border: 2px solid $secondaryBrandColor;
    border-radius: 10px;
    margin-top: 24px;
    cursor: pointer;
    @include desktop {
      width: 121px;
      margin-top: 32px;
    }
  }
}
.value {
  display: flex;
  gap: 9px;
  list-style-position: inside;
  margin-top: 22px;
  text-align: center;
  justify-content: center;
  @include desktop {
    margin-top: 24px;
  }
  &__item {
    width: 53px;
    height: 72px;
    border-radius: 40px;
    border: 2px solid #9cbe8f;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 1.22;
    color: #131313;
    @include desktop {
      font-size: 12px;
      width: 65px;
      height: 90px;
    }
  }
  &__roundborder {
    width: 43px;
    height: 43px;
    border-radius: 50px;
    background-color: #9cbe8f;
    border: 2px solid #9cbe8f;
    position: relative;
    top: -1px;
    left: 3px;
    box-shadow: 0 12px 25px #9cbe8f;
    @include desktop {
      width: 53px;
      height: 53px;
    }
  }
  &__number {
    position: relative;
    top: 6px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 10px;
    line-height: 2.8;
    color: #131313;
    margin: 0 auto;
    @include desktop {
      font-size: 12px;
      line-height: 2.3;
    }
  }
  &__legend {
    position: relative;
    top: 24px;
  }
}
.ingredients {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.4;
  color: #131313;
  text-align: center;
  &__box {
    margin-left: 20px;
    margin-right: 20px;
    @include desktop {
      margin-left: 40px;
      margin-right: 40px;
    }
  }
  &__list {
    flex-direction: column;
    gap: 11px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.76;
    color: #131313;
  }
}
.products-modal__form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center;
  width: 240px;
  height: 200px;
  margin: 0 auto;
  margin-top: 40px;
  @include desktop {
    width: 368px;
    margin-top: 48px;
  }
}
.products-modal__input {
  font-weight: 400;
  font-family: 'Montserrat', sans-serif;
  width: 240px;
  height: 42px;
  font-size: 14px;
  line-height: 1.76;
  background-color: rgba(248, 210, 78, 0.1);
  border: 1px solid $firstBrandColor;
  border-radius: 10px;
}
.products-modal__feedback {
  @extend .products-modal__input;
  height: 84px;
  resize: none;
}
.products-is-hidden {
  pointer-events: none;
  visibility: none;
  opacity: 0;
  transform: scale(0);
}
